@tailwind base;

@tailwind components;

@tailwind utilities;

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #1e1e2f;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: linear-gradient(
    0deg,
    rgb(51, 88, 244) 0%,
    rgb(29, 140, 248) 100%
  );
}

::-webkit-scrollbar-thumb:hover {
  background: rgb(29, 140, 248);
}

.transition-300-ease {
  transition: 300ms ease;
}

@-webkit-keyframes blink {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes blink {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
